html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  min-width: 320px;
  font-family: "Lato", sans-serif;
  font-size: 20px;
  color: #292929;
}

.container {
  margin: 0 auto;
  padding: 0 30px;
  max-width: 1429px;
}

.site-container {
  overflow: hidden;
}

img {
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.is-hidden {
  display: none !important;
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  overflow: hidden;
  border: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  padding: 18px 0;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo__svg {
  width: 79px;
  height: 23px;
  fill: #01996d;
}

.nav__list {
  margin: 0 100px 0 0;
}

.nav__item {
  display: inline-block;
}

.nav__item:not(:last-child) {
  margin: 0 70px 0 0;
}

.nav__link {
  font-weight: 500;
  line-height: 24px;
  transition: all 0.2s;
}

.nav__link:hover {
  color: #01996d;
}

.auth-list__item {
  display: inline-block;
}

.auth-list__item:first-child {
  margin: 0 36px 0 0;
}

.auth-list__link {
  line-height: 24px;
}

.auth-list__link:hover {
  color: #01996d;
}

.btn {
  display: block;
  border: 1px solid #01996d;
  border-radius: 4px;
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  background-color: #01996d;
  transition: all 0.2s;
}

.btn_size_s {
  width: 183px;
  height: 51px;
  line-height: 51px;
}

.btn_size_m {
  width: 227px;
  height: 65px;
  line-height: 65px;
}

.btn:hover {
  border-color: #01996d;
  color: #01996d;
  background-color: transparent;
}

.hero {
  padding: 174px 0 335px;
  background-image: url("../img/hero__bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.hero__title {
  max-width: 440px;
}

.hero__parag {
  margin-top: 34px;
  max-width: 420px;
  line-height: 28px;
}

.hero__btn {
  margin-top: 53px;
}

.title {
  font-family: "Georgia", sans-serif;
  font-weight: 700;
  font-size: 65px;
  line-height: 74px;
}

.title_color_main {
  color: #01996d;
}

.title_color_white {
  color: #fff;
}

.title_size_s {
  font-size: 50px;
}

.title_size_m {
  font-size: 65px;
}

@media (max-width: 1140px) {
  .nav {
    display: none;
  }

  .auth-list {
    display: none;
  }
}

@media (max-width: 650px) {
  body {
    font-size: 16px;
  }

  .container {
    padding: 0 15px;
  }

  .btn_size_m {
    width: 180px;
    height: 50px;
    line-height: 50px;
  }

  .hero__text {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

  .hero__parag {
    margin-top: 20px;
    line-height: 22px;
  }

  .hero__btn {
    margin-top: 40px;
  }

  .title_size_m {
    font-size: 50px;
    line-height: 60px;
  }
}